.soft{width:100%;}

#soft-top{
    margin-top: 3em;
    padding-bottom: 0.5em;
    text-align: center;
}
#soft-title{font-weight:bold; font-size:2em;}


.package-wrapper{
    width: 52em;
    height: 20em;
    margin: auto;
    margin-bottom: 5em;
    border-radius: 20px;
    box-shadow: 3px 3px 2px gray;
}
#package-title{margin-top: 0.4em; margin-left: 2em;}
#package-image{
    float: left;
    width: 13em;
    height: 13em;
    margin-top: 2em;
    margin-left: 4em;
    background: yellow;
}
#package-explain{
    float: left;
    width: 28em;
    margin-left: 4em;
    margin-top: 2em;
    text-align: center;
}
#package-link{
    width: 12em;
    height: 2em;
    margin-top: 2em;
}


#footer-empty{
    width: 100%;
    height: 5em;
}